    font-weight: 100;
    font-size: 14px;
    color: rgb(86, 100, 112);
}

header {
    background: #292929;
    padding: 20px 0;
}

header .logo a {
    display: inline-block;
    vertical-align: middle;
}

header .logo img {
    height: 50px !important;
}

header .menu {
    margin-left: 30px;
}

header .menu .menu-item {
    color: #fff;
    font-size: 16px;
    text-decoration: none;
    margin-right: 30px;
}

header .menu .menu-item:hover {
    text-decoration: none;
    font-weight: 400;
}

header .menu .menu-item:last-child {
    margin-right: 0 !important;
}

header .profile .photo img {
    height: 40px !important;
}

header .profile .profile-info .info1 {
    color: #fff;
}

header .profile .profile-info .info2 {
    color: #ffc75f;
}

.breadcrumb-bg {
    background: #e0e0e0 !important;
}

.breadcrumb-bg a {
    color: #020100;
}

.breadcrumb-bg .breadcrumb-item.active {
    color: #0a0a0a;
    font-weight: 500;
}

.tabcontent-border {
    border: 1px solid #ddd;
    border-top: 0px;
    padding: 20px;
}

.fa-white {
    color: #fff;
}

.pagination .active a {
    font-weight: bold;
    color: rgb(86, 100, 112);
}

.log-text {
    background: #333;
    color: #fff;
    padding: 10px;
    overflow: auto;
    height: 100px;
    width: 500px;
    margin: 0;
}

.menu-inferior {
    background-color: #214157;
    box-shadow: inset -1px 4px 5px #375a5952;
    height: 42px;
}

.menu-inferior ul {
    list-style: none;
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
}

.menu-inferior li {
    margin: 0;
    padding: 0 10px;
    transition-duration: 300ms;
}

.menu-inferior li:hover {
    background-color: #77a5b8;
}

.menu-inferior li.sub:hover > ul {
    display: block !important;
}

.menu-inferior li.sub {
    position: relative;
}

.menu-inferior li.sub ul {
    z-index: 10;
    display: none;
    background: #f9f9f9;
    left: 0;
    top: 42px;
    position: absolute;
    box-shadow: 0px 2px 3px 0px #ccc;
}

.menu-inferior li.sub.lado ul {
    background: #f9f9f9;
    left: 190px;
    top: 0;
}

.menu-inferior li.sub ul li:hover {
    background: #d1d1d1;;
}

.menu-inferior li.sub ul li a {
    color: #000;
    width: 180px;
}

.menu-inferior li a {
    display: block;
    text-decoration: none;
    font-size: 14px;
    color: black;
    height: 42px;
    line-height: 41px;
}

.menu-inferior li.sub a {
    color: #ffffff;
}

.menu-inferior li a i {
    color: #566470;
    display: inline-block;
    margin-right: 8px;
    margin-left: 10px;
}

.autocomplete {
    /*the container must be positioned relative:*/
    position: relative;
    display: inline-block;
}

.autocomplete-items {
    position: absolute;
    border: 1px solid #d4d4d4;
    border-bottom: none;
    border-top: none;
    z-index: 99;
    /*position the autocomplete items to be the same width as the container:*/
    top: 100%;
    left: 0;
    right: 0;
}

.autocomplete-items div {
    padding: 10px;
    cursor: pointer;
    background-color: #fff;
    border-bottom: 1px solid #d4d4d4;
}

.autocomplete-items div:hover {
    /*when hovering an item:*/
    background-color: #e9e9e9;
}

.autocomplete-active {
    /*when navigating through the items using the arrow keys:*/
    background-color: DodgerBlue !important;
    color: #ffffff;
}

.menu-mobile {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width:100%;
    z-index: 10;
    background-color: #214157;
